.hero {
  position: relative;
  height: 800px;
  overflow: hidden;
}
.slide {
  position: relative;
}
.slide:after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: #000;
    opacity: .50;
  }

.slide-caption{
  z-index: 19;
  padding-bottom: 30px;
}
.hero-style-1 .slide-caption {
    z-index: 19;
    padding-bottom: 20px;
}
.hero .slide {
  height: 800px;
  position: relative;
  background-repeat: no-repeat;
  display: flex!important;
  justify-content: center;
  flex-direction: column;
}
.slide1{
  background: url(/static/media/3.841cca7d.jpg) center center/cover no-repeat local;
}
.slide2{
  background: url(/static/media/1.841cca7d.jpg) center center/cover no-repeat local;
}
.slide3{
  background: url(/static/media/4.841cca7d.jpg) center center/cover no-repeat local;
}

.hero .slide .slider-bg {
  display: none;
}

.hero .slide:focus {
  outline: none;
}

.hero .slide .container {
  height: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.slick-prev, .slick-next {
    width: 50px;
    height: 50px;
    z-index: 10;
    transition: all .3s;
    background:rgba(255,255,255,.3); 
}

.slick-prev {
  left: 25px;
}

.slick-prev:before {
  font-family: "FontAwesome";
  content: "\f104";
  opacity: 1;
  color: #fff;
}

.slick-next {
  right: 25px;
}

.slick-next:before {
  font-family: "FontAwesome";
  content: "\f105";
  opacity: 1;
  color: #fff;
}

.slick-prev:hover,
.slick-next:hover {
  background: #d0a15e;
  border:none;
}

.slick-prev:hover:before,
.slick-next:hover:before {
  color: #fff;
}

.hero .slick-dots {
  bottom: 35px;
  color: red;
}

.hero .slick-dots li {
  margin: 10px;
}

.slick-dots li.slick-active button:before {
    opacity:1;
    color: #d0a15e;
    border:1px solid #d0a15e;
    border-radius: 100%;
}
.slick-dots li button:hover:before, .slick-dots li button:focus:before {
    opacity: 1;
}

.slick-dots li button:before {
    font-size: 12px;
    line-height: 25px;
    width: 25px;
    height: 25px;
    color: #fff;
    opacity: 1;
}





